.homeWrap {
    .article1 {
    }
    .article2 {
        .row {
            .local {
                display: flex;
                flex-wrap: wrap;
                padding: 30px;
                li {
                    width: 33.33%;
                    padding: 20px;
                    a {
                        display: flex;
                        align-items: center;
                        img {
                            width: 50%;
                            margin-right: 10px;
                            border-radius: 10px;
                        }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size: 20px; }
                ul {
                    display: flex;
                    li {
                        flex: 1;
                        padding: 20px;
                    }
                }
            }
        }
    }
    .article3 {
        background: #000;
        color: #fff;
        padding: 100px 0px;
        .row {
            display: flex;
            div {
                flex: 1.135;
                img {
                    padding: 20px 10px 0px;
                    border-radius: 40px 40px 0px 0px;
                }
                p {
                    background: #999;
                    margin: 0 10px;
                    padding: 20px;
                    border-radius: 0 0 25px 25px;
                }
                &.right {
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .rightRow1 {
                        display: flex;
                        flex-direction: row;
                    }
                }
            }
        }
    }
}